"use client";

import { FormEvent, useState } from "react";

export default function FamilyOnboardingPage() {
  const [fullName, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [neighborhood, setNeighborhood] = useState("");
  const [kids, setKids] = useState("");
  const [supportNeeds, setSupportNeeds] = useState("");
  const [comforts, setComforts] = useState("");
  const [notes, setNotes] = useState("");
  const [submitted, setSubmitted] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setError(null);
    setSubmitted(false);

    if (!fullName || !email || !neighborhood || !kids || !supportNeeds) {
      setError("Please fill in the required fields.");
      return;
    }

    console.log("Family onboarding", {
      fullName,
      email,
      neighborhood,
      kids,
      supportNeeds,
      comforts,
      notes,
    });

    setSubmitted(true);
  };

  return (
    <section className="families-auth">
      <div className="families-auth-inner">
        <div className="families-hero">
          <p className="eyebrow">Families</p>
          <h1>Complete your ShiftSitter onboarding</h1>
          <p className="lead">
            Add a few details so we can verify your profile, understand your schedule,
            and match you with nearby families for reciprocal care.
          </p>
          <ul className="families-bullets">
            <li>Designed for shift schedules: nights, weekends, rotating weeks.</li>
            <li>Matches pair you with nearby parents who share availability and values.</li>
            <li>Introductions happen before any swap so everyone feels ready.</li>
          </ul>
        </div>

        <div className="families-card">
          <div className="card-header">
            <h2>Parent onboarding form</h2>
            <p className="muted">
              Provide the essentials below. We will follow up with verification steps
              and your first match recommendations.
            </p>
          </div>

          <form onSubmit={handleSubmit}>
            <div className="form-field">
              <label htmlFor="fullName">Full name</label>
              <input
                id="fullName"
                type="text"
                className="ss-input"
                placeholder="Taylor Morgan"
                value={fullName}
                onChange={(e) => setFullName(e.target.value)}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="email">Email</label>
              <input
                id="email"
                type="email"
                className="ss-input"
                placeholder="you@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="neighborhood">Neighborhood or ZIP</label>
              <input
                id="neighborhood"
                type="text"
                className="ss-input"
                placeholder="Fells Point, 21231"
                value={neighborhood}
                onChange={(e) => setNeighborhood(e.target.value)}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="kids">Kids and ages</label>
              <textarea
                id="kids"
                className="ss-input"
                placeholder="Ava (4), Lucas (7)"
                value={kids}
                onChange={(e) => setKids(e.target.value)}
                rows={2}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="supportNeeds">Support windows needed</label>
              <textarea
                id="supportNeeds"
                className="ss-input"
                placeholder="Weeknights 6:30pm-11pm; every other Saturday 7am-3pm"
                value={supportNeeds}
                onChange={(e) => setSupportNeeds(e.target.value)}
                rows={3}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="comforts">Comforts and preferences</label>
              <textarea
                id="comforts"
                className="ss-input"
                placeholder="Transportation limits, pet allergies, preferred introductions, screen time rules"
                value={comforts}
                onChange={(e) => setComforts(e.target.value)}
                rows={3}
              />
            </div>

            <div className="form-field">
              <label htmlFor="notes">Notes (optional)</label>
              <textarea
                id="notes"
                className="ss-input"
                placeholder="Anything else you want us to know."
                value={notes}
                onChange={(e) => setNotes(e.target.value)}
                rows={3}
              />
            </div>

            {error && <p className="form-error">{error}</p>}
            {submitted && (
              <p
                style={{
                  margin: "4px 0 12px",
                  color: "#0f766e",
                  fontWeight: 700,
                }}
              >
                Thanks for completing onboarding. You are in the queue for matches and
                verification steps.
              </p>
            )}

            <div className="actions">
              <button type="submit" className="ss-btn w-100">
                Submit onboarding
              </button>
              <p className="muted" style={{ margin: 0, fontSize: "0.95rem" }}>
                We only use this to match you and coordinate safe introductions.
              </p>
            </div>
          </form>
        </div>
      </div>
    </section>
  );
}
